import React from "react";
import firebase from "../firebase";
import { auth } from "../auth";
import {
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import NavDrawer from "./NavDrawer";
import { Delete, Edit } from "@material-ui/icons";

class Submissions extends React.Component {
  constructor(props) {
    super(props);
    this.state = { records: {} };
  }

  databaseCallback = (records) => this.setState({ records: records.toJSON() });

  async componentDidMount() {
    if (auth.currentUser) {
      firebase
        .database()
        .ref(`test/users`)
        .child(auth.currentUser.uid)
        .on("value", this.databaseCallback);
    }
  }
  editRecord(key) {
    this.props.history.push(`/new/${key}`);
  }
  deleteRecord(key) {
    if (auth.currentUser) {
      firebase
        .database()
        .ref(`test/users`)
        .child(auth.currentUser.uid)
        .child(key)
        .remove();
    }
  }
  render() {
    return (
      <NavDrawer>
        <h1>Submission list</h1>
        {this.state.records && Object.keys(this.state.records).length > 0 ? (
          <div>
            <Typography>These are the submissions we have received:</Typography>
            <List>
              {Object.entries(this.state.records).map(([key, val]) => (
                <ListItem button key={key}>
                  <ListItemText primary={val.title.en} />
                  <ListItemIcon onClick={() => this.editRecord(key)}>
                    <Edit />
                  </ListItemIcon>
                  <ListItemIcon onClick={() => this.deleteRecord(key)}>
                    <Delete />
                  </ListItemIcon>
                </ListItem>
              ))}
            </List>
          </div>
        ) : (
          <Typography>No records submitted yet!</Typography>
        )}
      </NavDrawer>
    );
  }
}

export default Submissions;
